<template>
  <div class="app-desc" id="">
    <div class="desc-title">
      <div class="h-50"><p></p></div>
      <div class="desc-name">
        {{ descInfo.name }}
      </div>
      <p> {{ descInfo.count }}次下载 </p>
      <p>  {{ descInfo.size }} KB </p>

      <div class="h-15"></div>
      <div class="detail-expain">
        <p>{{ descInfo.desc }}</p>
      </div>
      <div class="h-15"></div>
      <div class="desc-down">
        <a :href="descInfo.down"><mt-button size="large" type="primary">点击下载</mt-button></a>
      </div>
      <div class="h-15"></div>

    </div>
    <div class="desc-body">
      <div class="h-15"></div>
      <div class="desc-pic">
        <div class="h-15"></div>
        <div class="desc-pic-wrapper">
          <mt-swipe :auto="4000">


            <mt-swipe-item>
              <img src="http://pp.myapp.com/ma_pic2/0/shot_42367467_1_1476240029/550" alt="" style="min-width:150px;">
            </mt-swipe-item>

            <mt-swipe-item><img src="http://pp.myapp.com/ma_pic2/0/shot_42367467_2_1476240029/550"
                                alt="" style="min-width:150px;"></mt-swipe-item>
            <mt-swipe-item><img src="http://pp.myapp.com/ma_pic2/0/shot_42367467_3_1476240029/550"
                                alt="" style="min-width:150px;"></mt-swipe-item>
            <mt-swipe-item><img src="http://pp.myapp.com/ma_pic2/0/shot_42367467_3_1476240029/550"
                                alt="" style="min-width:150px;"></mt-swipe-item>
            <mt-swipe-item><img src="http://pp.myapp.com/ma_pic2/0/shot_42367467_3_1476240029/550"
                                alt="" style="min-width:150px;"></mt-swipe-item>
          </mt-swipe>
        </div>
        <div class="h-15"></div>
        <p>版本</p>
        <p>更新时间</p>
      </div>
      <div class="h-15"></div>

    </div>


  </div>
</template>

<script>

  import axios from 'axios'

  export default {
    name: 'softDesc',
    data () {
      return {
        descId: 100000,
        descInfo: {}
      }
    },
    props: ['did'],
    created () {
      // 组件创建完后获取数据，
      // 此时 data 已经被 observed 了
      this.loadDesc()
    },
    methods: {
      test () {
        console.log(this.$route.params.id)
      },
      loadDesc () {
          let _this = this
          axios({
            method: 'post',
            url: '/api/soft/desc',
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            data: {
              id: _this.descId
            }
          }).then(function (response) {
            console.log(response.data.data)
            _this.descInfo = response.data.data
          })
      }

    }

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .app-desc
    background #EEE
    .desc-title
      width 100%
      background #FFF
      box-shadow 0 2px 10px 2px #AAA
      .h-50{
        height 50px
      }
      .desc-name
        height 40px;
        text-align center
        align-content center
        line-height 40px
        font-size 24px
      .desc-down
        margin 10px 0

    .desc-body
      height 100%
      min-height 700px
      width 100%
      .h-15
        height 15px
        width 100%
      .desc-pic
        height 680px;
        width 94%
        margin 0 auto
        background #FFF
        box-shadow 0 2px 10px 2px #AAA
        position relative
        .desc-pic-wrapper
          width 100%
          max-width 400px
          height 600px
          margin 0 auto
          img
            width 100%
            height 100%

</style>



