      <softDesc :did="descId"></softDesc>
    </div>
  </div>
</template>

<script>
  let a = []
  let b = []

  import axios from 'axios'
  import softDesc from '@/components/page/SoftDesc.vue'

  export default {
    name: 'soft',
    data () {
      return {
        msg: '',
        list: a,
        tPage: 1,
        allLoaded: false,
        bottomStatus: '',
        loading: false,
        descId: 100000,
        show: {
          desc: false
        }
      }
    },
    created () {
      // 组件创建完后获取数据，
      // 此时 data 已经被 observed 了
      this.loadMore()
    },
    methods: {
      handleBottomChange (status) {
        this.bottomStatus = status
      },
      catDesc (id) {
        this.descId = id
        this.show.desc = true
      },
      closeDesc () {
        this.show.desc = false
      },
      loadBottom () {
//        let _this = this
//        axios({
//          method: 'post',
//          url: 'http://www.a.com/api/soft/list',
//          headers: {
//            // 'Accept': 'application/json'
//            // 'Content-Type': 'application/json'
//          },
//          data: {
//            page: _this.tPage
//          }
//        }).then(function (response) {
//          console.log(response)
//          b = response.data.data
//          _this.tPage += 1
//          // console.log(b)
//          if (_this.list.length === 0) {
//            _this.loading = false
//            return
