      },
      loadMore () {
        setTimeout(() => {
          let _this = this
          _this.loading = true
          console.log(_this.tPage)
          axios({
            method: 'post',
            url: '/api/soft/list',
            headers: {
              // 'Accept': 'application/json'
              // 'Content-Type': 'application/json'
            },
            data: {
              page: _this.tPage
            }
          }).then(function (response) {
            console.log(response)
            b = response.data.data
            _this.tPage += 1
            // console.log(b)
            if (_this.list.length > 0) {
              a = [...a, ...b]
            } else {
              a = b
            }
            _this.loading = false
            setTimeout(() => {
              _this.list = a
            }, 20)
          }).catch(function (error) {
            console.log(error)
          })
        }, 2500)
        //        this.allLoaded = true
      }
    },
    components: {
      softDesc
    }
  }
